.profile__info {
  display: grid;
  grid-template-columns: auto auto;
  row-gap: 8px;
  column-gap: 18px;
  justify-content: center;
}

@media screen and (max-width: 1024px) {
  .profile__info {
    gap: 8px;

    display: flex;
    flex-direction: column;
    align-items: center;
   
  }
}
